<template>
  <div id="ledgerAdd">
    <Modal
      v-model="build"
      title="新建人类活动监管台账信息"
      @on-cancel="reset"
    >
      <Form ref="addForm" :model="AddData" :rules="ruleValidate" :label-width="100" style="padding:0 20%;">
        <FormItem prop="num" label="问题点编号：">
          <Input v-model.trim="AddData.num" placeholder="请输入问题点编号" size="default"/>
        </FormItem>
        <FormItem prop="type" label="所属任务：">
          <Select v-model.trim="AddData.type" size="default" placeholder="请选择所属任务">
            <Option v-for="item in addTypeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </FormItem>
        <FormItem prop="lon" label="经度：">
          <Input v-model.trim="AddData.lon" placeholder="请输入经度" size="default"/>
        </FormItem>
        <FormItem prop="lat" label="纬度：">
          <Input v-model.trim="AddData.lat" placeholder="请输入纬度" size="default"/>
        </FormItem>
        <FormItem prop="typeP" label="问题点类型：">
          <Select v-model.trim="AddData.typeP" size="default" placeholder="请选择问题点类型">
            <Option v-for="item in dotList1" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </FormItem>
        <FormItem prop="desc" label="问题点备注：">
          <Input v-model.trim="AddData.desc" type="textarea" :rows="4" placeholder="请输入问题点备注" size="default"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import {
  // 新建活动核查---暂时
  saveFiled
} from '@/api/dataCollection'
export default {
  name: 'TaskAdd',
  data () {
    return {
      /* 类型参数 */
      addTypeList: [
        {
          value: '1',
          label: 'type1'
        },
        {
          value: '2',
          label: 'type2'
        }
      ],
      /* 问题点类型参数 */
      dotList1: [
        {
          value: '1',
          label: 'type1'
        },
        {
          value: '2',
          label: 'type2'
        }
      ],
      /* 添加数据表单参数 */
      AddData: {
        num: '',
        type: '',
        lon: '',
        lat: '',
        typeP: '',
        desc: ''
      },
      /* 添加数据表单校验 */
      ruleValidate: {
        num: [
          { required: true, message: '任务名称必须填写', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '所属任务必须选择', trigger: 'change' }
        ]
      },
      /* 新建框 */
      build: false
    }
  },
  mounted () {},
  methods: {
    /* 添加核查任务 */
    addSubmit () {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          saveFiled({
            data: {
              num: this.AddData.name,
              type: this.AddData.type,
              lon: this.AddData.lon,
              lat: this.AddData.lat,
              typeP: this.AddData.typeP,
              desc: this.AddData.desc
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '新建人类活动监管台账信息成功' })
              this.$emit('getTableData')
              this.build = false
              this.AddData = {}
            } else {
              this.$Notice.error({ title: '新建人类活动监管台账信息失败' })
            }
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.AddData = {
        num: '',
        type: '',
        lon: '',
        lat: '',
        typeP: '',
        desc: ''
      }
      this.$refs.addForm.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
