<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 300px)"></QueryBox>
      <div class="queryBtns">
        <Button type="primary" size="small" class="btn-tabBtn" @click.stop="addActive=true;$refs.ledgerManageMap.initCom()"><i class="iconfont icon-leimupinleifenleileibie"></i>切换地图</Button>
        <Button type="success" size="small" class="btn-download" style="padding: 2px 7px 1.8px;margin-right: 10px">
          <Upload
            :show-upload-list="false"
            :format="['.xls']"
            action="#">
            <i class="iconfont icon-upload"></i>
            <span>导入</span>
          </Upload>
        </Button>
        <Button type="primary" size="small" class="btn-exportBtn" style="margin-right: 18px" @click.stop="handleIssue"><i class="iconfont icon-xiazai"></i>下发</Button>
        <Button type="success" size="small" icon="md-add" class="btn-add" @click.stop="$refs.ledgerAdd.build = true">新建</Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"></Table>
        <div class="page_box">
          <Page :total="total" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- 查看-->
    <div class="extract_box" :class="{addActive:addActive}">
      <ledgerManageMap ref="ledgerManageMap" @signOut="signOut"></ledgerManageMap>
    </div>
    <!-- 详情列表 -->
    <div class="extract_box" :class="{detailActive:detailActive}">
      <div>
        <Table :data="data2" :columns="columns2"></Table>
        <Page :total="total1"  show-total :page-size="pageSize1" :current.sync="pageNum1"/>
      </div>
      <div style="padding: 10px">
        <Button type="default" size="small" @click="detailActive=false">返回</Button>
      </div>
    </div>
    <!-- 新建-->
    <ledgerAdd ref="ledgerAdd" @getTableData="getTableList"></ledgerAdd>
    <!-- 修改-->
    <ledgerEdit ref="ledgerEdit" :data="editDefaultData" @getTableData="getTableList"></ledgerEdit>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import ledgerManageMap from './ledgerManage_map'
import ledgerAdd from './ledgerAdd'
import ledgerEdit from './ledgerEdit'
import {
  getAccountList,
  rlhdDetail
} from '@/api/remoteSensing'
export default {
  name: 'extracTable',
  data () {
    return {
      /* 查询框查询条件 */
      queryData: {},
      typeList: [
        {
          value: '',
          label: '请选择'
        },
        {
          value: '1',
          label: 'type1'
        },
        {
          value: '2',
          label: 'type2'
        }
      ],
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 表格数据 */
      columns1: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '台账名称',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '说明',
          align: 'center',
          minWidth: 200,
          key: 'remark'
        },
        {
          title: '人类活动版块数量',
          align: 'center',
          minWidth: 200,
          key: 'sonCount'
        },
        {
          title: '操作',
          key: 'address',
          width: 250,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-eye'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    /* this.addActive = true
                    this.$refs.ledgerManageMap.initCom() */
                    this.showAccountDetail(params.row.id)
                  }
                }
              }, '查看详情')
            ])
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      loading: false,
      /* 提取信息框状态 */
      addActive: false,
      /* 编辑框默认原数据 */
      editDefaultData: {},
      /* 台账详情面板 */
      detailActive: false,
      /* 人类活动详情列表数据 */
      data2: [],
      /* 人类活动详情列表的列设置 */
      columns2: [
        {
          type: 'index',
          align: 'center'
        },
        {
          title: '活动名称',
          align: 'center',
          minWidth: 200,
          key: 'activeName'
        },
        {
          title: '活动类型',
          align: 'center',
          minWidth: 200,
          key: 'activeType'
        },
        {
          title: '说明',
          align: 'center',
          minWidth: 200,
          key: 'descri'
        },
        {
          title: '备注',
          align: 'center',
          minWidth: 200,
          key: 'remark'
        },
        {
          title: '面积（公顷）',
          align: 'center',
          minWidth: 200,
          key: 'area'
        }
      ],
      /* 人类活动详情列表的分页器参数 */
      pageSize1: 20,
      pageNum1: 10,
      total1: 0
    }
  },
  components: {
    QueryBox,
    // 台账信息地图
    ledgerManageMap,
    // 新建弹窗
    ledgerAdd,
    // 编辑弹窗
    ledgerEdit
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getAccountList({
        'data': {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* axios: 删除数据 */
    infoDel (e) {
      infoDel({
        data: {
          id: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableList()
          this.$Notice.success({ title: '删除成功' })
        } else {
          this.$Notice.error({ title: '删除失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 信息提取地图页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    },
    /* 展开显示人类活动台账的详情 */
    showAccountDetail (id) {
      this.detailActive = true
      this.pageNum1 = 1
      rlhdDetail({
        data: {
          id: id,
          pageSize: this.pageSize1,
          pageNum: this.pageNum1
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.data2 = res.data.data.rows
          this.total1 = res.data.data.total
        } else {
          this.$Notice.error({ title: '获取人类活动台账详情失败', desc: res.data.msg })
        }
      })
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .btn-download {
      background-color: #1ccf20;
      border-color: #1ccf20;
      &:hover, &:focus {
        background-color: #13bb17;
        border-color: #13bb17;
      }
    }
    .btn-tabBtn {
      background: rgb(16,186,204);
      border-color: rgb(16,186,204);
      margin-right: 10px;
      &:hover {
        background: #13B1CC;
        border-color: #13B1CC;
      }
    }
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
      &.icon-leimupinleifenleileibie {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .extract_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
    .detailActive{
      right: 0;
    }
  }
</style>
