<template>
  <div id="ledgerEdit">
    <Modal
      v-model="edit"
      title="修改人类活动监管台账信息"
      @on-cancel="reset">
      <Form ref="editForm" :model="EditData" :rules="EditRule" :label-width="100" style="padding:0 20%;">
        <FormItem prop="id" label="ID：" style="display: none">
          <Input v-model.trim="EditData.id"/>
        </FormItem>
        <FormItem prop="num" label="问题点编号：">
          <Input v-model.trim="EditData.num" placeholder="请输入问题点编号" size="default"/>
        </FormItem>
        <FormItem prop="type" label="所属任务：">
          <Select v-model.trim="EditData.type" size="default" placeholder="请选择所属任务">
            <Option v-for="item in addTypeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </FormItem>
        <FormItem prop="lon" label="经度：">
          <Input v-model.trim="EditData.lon" placeholder="请输入经度" size="default"/>
        </FormItem>
        <FormItem prop="lat" label="纬度：">
          <Input v-model.trim="EditData.lat" placeholder="请输入纬度" size="default"/>
        </FormItem>
        <FormItem prop="typeP" label="问题点类型：">
          <Select v-model.trim="EditData.typeP" size="default" placeholder="请选择问题点类型">
            <Option v-for="item in dotList1" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </FormItem>
        <FormItem prop="desc" label="问题点备注：">
          <Input v-model.trim="EditData.desc" type="textarea" :rows="4" placeholder="请输入问题点备注" size="default"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="editSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import {
  // 编辑活动核查---暂时
  saveFiled
} from '@/api/dataCollection'
export default {
  name: 'Taskedit',
  data () {
    return {
      /* 所属任务列表 */
      addTypeList: [
        {
          value: '1',
          label: 'type1'
        },
        {
          value: '2',
          label: 'type2'
        }
      ],
      /* 问题点类型参数 */
      dotList1: [
        {
          value: '1',
          label: 'type1'
        },
        {
          value: '2',
          label: 'type2'
        }
      ],
      /* 数据表单校验 */
      EditRule: {
        num: [
          { required: true, message: '任务名称必须填写', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '所属任务必须选择', trigger: 'change' }
        ]
      },
      /* 编辑框 */
      edit: false,
      /* 编辑数据 */
      EditData: {}
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  mounted () {},
  methods: {
    /* 编辑框默认值数据处理 */
    dataHandle () {
      this.EditData.id = this.data.id
      this.EditData.num = this.data.num
      this.EditData.type = this.data.type
      this.EditData.lon = this.data.lon
      this.EditData.lat = this.data.lat
      this.EditData.typeP = this.data.typeP
      this.EditData.desc = this.data.desc
    },
    /* 编辑核查任务 */
    editSubmit () {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          saveFiled({
            data: {
              id: this.EditData.id,
              num: this.EditData.name,
              type: this.EditData.type,
              lon: this.EditData.lon,
              lat: this.EditData.lat,
              typeP: this.EditData.typeP,
              desc: this.EditData.desc
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '编辑人类活动监管台账信息成功' })
              this.$emit('getTableData')
              this.edit = false
              this.EditData = {}
            } else {
              this.$Notice.error({ title: '新建人类活动监管台账信息失败' })
            }
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.EditData = {
        id: '',
        num: '',
        type: '',
        lon: '',
        lat: '',
        typeP: '',
        desc: ''
      }
      this.EditData = {}
      this.$refs.editForm.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
